//  
interface Library {
  id: number;
  name: string;
  address: string;
  phone: string;
  hours?: string;
}

interface AutocompleteItemData {
  text: string;
  category: string;
  type: 'popular' | 'recent' | 'suggestion';
}

interface Book {
  id: string;
  title: string;
  author: string;
  publisher: string;
  isbn: string;
  category: string;
  publishYear: string;
  description?: string;
  imageUrl?: string;
  loanCount?: number;
  availability: Array<{
    libraryId: number;
    libraryName: string;
    libraryAddress: string;
    libraryPhone: string;
    libraryHours?: string;
    available: boolean;
    waitingList: number;
    totalBooks: number;
    availableBooks: number;
    location: string;
  }>;
}

interface BookSearchSectionProps {
  externalSearchQuery?: string;
  onExternalSearchComplete?: () => void;
  selectedRegion?: string;
  onRegionUpdate?: (region: string) => void;
}

const BookSearchSection: React.FC<BookSearchSectionProps> = ({ 
  externalSearchQuery, 
  onExternalSearchComplete,
  selectedRegion: parentSelectedRegion,
  onRegionUpdate 
}) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState<Book[]>([]);
  const [loading, setLoading] = useState(false);
  const [hasSearched, setHasSearched] = useState(false);
  const [selectedRegion, setSelectedRegion] = useState(parentSelectedRegion || '');
  const [libraries, setLibraries] = useState<Library[]>([]);
  const [apiStatus, setApiStatus] = useState<string>('');
  const [apiError, setApiError] = useState<string>('');

  //   
  const [recentSearches, setRecentSearches] = useState<string[]>([]);

  //  
  const [showAutocomplete, setShowAutocomplete] = useState(false);
  const [autocompleteItems, setAutocompleteItems] = useState<AutocompleteItemData[]>([]);

  //  
  const [bookmarks, setBookmarks] = useState<string[]>([]);



  //   
  const popularSearches = [
    { text: ' 6', category: '' },
    { text: ' ', category: '' },
    { text: ' ', category: '' },
    { text: '82 ', category: '' },
    { text: '', category: '' },
    { text: ' ', category: '' },
    { text: '', category: '' },
    { text: ' ', category: '' },
    { text: ' ', category: '' },
    { text: ' ', category: '' },
    { text: '', category: '' },
    { text: ' ', category: '' },
    { text: '', category: '' },
    { text: '', category: '' },
    { text: '', category: '' }
  ];

  //  
  const updateAutocomplete = (inputValue: string) => {
    if (!inputValue.trim()) {
      const items: AutocompleteItemData[] = [
        ...recentSearches.slice(0, 5).map(search => ({
          text: search,
          category: ' ',
          type: 'recent' as const
        })),
        ...popularSearches.slice(0, 5).map(search => ({
          text: search.text,
          category: search.category,
          type: 'popular' as const
        }))
      ];
      setAutocompleteItems(items);
      setShowAutocomplete(true);
      return;
    }

    const searchValue = inputValue.toLowerCase();
    const matchedItems: AutocompleteItemData[] = [];

    recentSearches.forEach(search => {
      if (search.toLowerCase().includes(searchValue)) {
        matchedItems.push({
          text: search,
          category: ' ',
          type: 'recent'
        });
      }
    });

    popularSearches.forEach(search => {
      if (search.text.toLowerCase().includes(searchValue)) {
        matchedItems.push({
          text: search.text,
          category: search.category,
          type: 'popular'
        });
      }
    });

    setAutocompleteItems(matchedItems.slice(0, 10));
    setShowAutocomplete(matchedItems.length > 0);
  };

  //    
  const handleAutocompleteSelect = (item: AutocompleteItemData) => {
    setSearchTerm(item.text);
    setShowAutocomplete(false);
    saveRecentSearch(item.text);
    performSearch(item.text);
  };

  //   
  const handleSearchInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchTerm(value);
    
    setTimeout(() => {
      updateAutocomplete(value);
    }, 300);
  };

  //    
  const handleSearchInputFocus = () => {
    updateAutocomplete(searchTerm);
  };

  //  
  const handleAutocompleteClose = () => {
    setTimeout(() => setShowAutocomplete(false), 150);
  };

  //        
  useEffect(() => {
    const savedSearches = localStorage.getItem('recentSearches');
    if (savedSearches) {
      setRecentSearches(JSON.parse(savedSearches));
    }
    
    const savedBookmarks = localStorage.getItem('bookmarks');
    if (savedBookmarks) {
      setBookmarks(JSON.parse(savedBookmarks));
    }
  }, []);

  //   
  const saveRecentSearch = (query: string) => {
    const updated = [query, ...recentSearches.filter(s => s !== query)].slice(0, 10);
    setRecentSearches(updated);
    localStorage.setItem('recentSearches', JSON.stringify(updated));
  };

  //  
  const toggleBookmark = (bookId: string) => {
    let updated: string[];
    if (bookmarks.includes(bookId)) {
      updated = bookmarks.filter(id => id !== bookId);
    } else {
      updated = [...bookmarks, bookId];
    }
    setBookmarks(updated);
    localStorage.setItem('bookmarks', JSON.stringify(updated));
  };

  //   
  const isBookmarked = (bookId: string) => bookmarks.includes(bookId);

  // API   
  useEffect(() => {
    const checkApprovalStatus = async () => {
      try {
        const isApproved = await checkApiApprovalStatus('651824a6d5a5d765b513f7f8059ef5ffb2ac3c30f15f0114a8764076c8b902b8');
        if (isApproved) {
          setApiStatus('   API ');
        } else {
          setApiStatus(' API    -   ');
        }
      } catch (error) {
        setApiStatus(' API    -   ');
      }
    };

    checkApprovalStatus();
  }, []);

  //   
  const generateLibrariesByRegion = (region: string): Library[] => {
    const regionLower = region.toLowerCase();
    //    
    if (regionLower.includes('')) {
      const cityName = region.replace('', '');
      return [
        { id: 1, name: `${cityName}  `, address: ` ${cityName}  123`, phone: '031-123-4567', hours: '09:00-18:00' },
        { id: 2, name: `${cityName}  `, address: ` ${cityName}  2`, phone: '031-234-5678', hours: '10:00-20:00' },
        { id: 3, name: `${cityName} `, address: ` ${cityName}   `, phone: '031-345-6789', hours: '10:00-18:00' }
      ];
    }
    
    //     
    const libraryData: { [key: string]: Library[] } = {
      '': [
        { id: 1, name: '', address: '    183', phone: '031-228-4300', hours: '09:00-22:00' },
        { id: 2, name: '', address: '    235', phone: '031-249-4800', hours: '09:00-18:00' },
        { id: 3, name: '', address: '    999-1', phone: '031-228-4350', hours: '09:00-20:00' }
      ],
      '': [
        { id: 1, name: '', address: '    346', phone: '031-729-4600', hours: '09:00-18:00' },
        { id: 2, name: '', address: '    178-1', phone: '031-729-4650', hours: '09:00-20:00' },
        { id: 3, name: '', address: '    271-3', phone: '031-729-4670', hours: '10:00-18:00' }
      ],
      '': [
        { id: 1, name: '', address: '    1955', phone: '031-8075-9300', hours: '09:00-18:00' },
        { id: 2, name: '', address: '    1256', phone: '031-8075-9350', hours: '09:00-20:00' },
        { id: 3, name: '', address: '    1234-5', phone: '031-8075-9380', hours: '10:00-18:00' }
      ],
      '': [
        { id: 1, name: '', address: '    1', phone: '032-320-3901', hours: '09:00-18:00' },
        { id: 2, name: '', address: '    7', phone: '032-320-3902', hours: '09:00-20:00' },
        { id: 3, name: '', address: '    92', phone: '032-320-3903', hours: '10:00-18:00' }
      ],
      '': [
        { id: 1, name: '', address: '    86', phone: '031-8045-2600', hours: '09:00-18:00' },
        { id: 2, name: '', address: '    80', phone: '031-8045-2650', hours: '09:00-20:00' },
        { id: 3, name: '', address: '    103', phone: '031-8045-2700', hours: '10:00-18:00' }
      ],
      '': [
        { id: 1, name: '', address: '    1199', phone: '031-324-4600', hours: '09:00-18:00' },
        { id: 2, name: '', address: '    19', phone: '031-324-4650', hours: '09:00-20:00' },
        { id: 3, name: '', address: '    435', phone: '031-324-4700', hours: '10:00-18:00' },
        { id: 4, name: '', address: '    83', phone: '031-324-4680', hours: '09:00-18:00' }
      ]
    };
    
    //    (  )
    for (const [key, data] of Object.entries(libraryData)) {
      if (regionLower.includes(key)) {
        return data;
      }
    }
    
    // :   
    return [
      { id: 1, name: '', address: '    235', phone: '031-249-4800', hours: '09:00-18:00' },
      { id: 2, name: '', address: '  2 84', phone: '02-2150-3001', hours: '09:00-20:00' },
      { id: 3, name: '', address: '   51', phone: '031-659-4800', hours: '10:00-18:00' }
    ];
  };

  //    
  const generateLibraryAvailability = (libraries: Library[]) => {
    return libraries.map(library => {
      const totalBooks = Math.floor(Math.random() * 8) + 3; // 3-10
      const availableBooks = Math.floor(Math.random() * (totalBooks + 1)); // 0 ~ totalBooks
      const waitingList = Math.floor(Math.random() * 6); // 0-5
      const available = availableBooks > 0; //     true
      
      //     
      const floor = Math.floor(Math.random() * 5) + 1; // 1-5
      const section = ['', '', '', '', '', '', ''][Math.floor(Math.random() * 7)];
      const aisle = String.fromCharCode(65 + Math.floor(Math.random() * 8)); // A-H
      const callNumber = `${Math.floor(Math.random() * 900) + 100}.${Math.floor(Math.random() * 99) + 1}`;
      
      return {
        libraryId: library.id,
        libraryName: library.name,
        libraryAddress: library.address,
        libraryPhone: library.phone,
        libraryHours: library.hours,
        available: available,
        waitingList: waitingList,
        totalBooks: totalBooks,
        availableBooks: availableBooks,
        location: `${floor} ${section} ${aisle} | : ${callNumber}`
      };
    });
  };

  //  
  const performSearch = async (query: string) => {
    setLoading(true);
    setHasSearched(true);
    setApiError('');

    //      
    if (!selectedRegion) {
      setSelectedRegion('');
      const defaultLibraries = generateLibrariesByRegion('');
      setLibraries(defaultLibraries);
    }

    try {
      const results = await searchBooksAPI(query, 1, 20);
      
      if (results && results.length > 0) {
        setApiStatus('     ');
        
        const currentLibraries = libraries.length > 0 ? libraries : generateLibrariesByRegion('');
        const booksWithAvailability = results.map(book => ({
          ...book,
          availability: generateLibraryAvailability(currentLibraries)
        }));
        
        setSearchResults(booksWithAvailability);
      } else {
        setApiStatus('  API   -   ');
        const dummyResults = getDummySearchResults(query);
        setSearchResults(dummyResults);
      }
    } catch (error) {
      console.error('  :', error);
      setApiError('API   .   .');
      setApiStatus(' API   -   ');
      
      const dummyResults = getDummySearchResults(query);
      setSearchResults(dummyResults);
    } finally {
      setLoading(false);
    }
  };

  //    
  const getDummySearchResults = (query: string): Book[] => {
    const dummyBooks = [
      {
        id: '1',
        title: '  ',
        author: 'J.K. ',
        publisher: '',
        isbn: '9788983920072',
        category: '',
        publishYear: '1997',
        description: '       ',
        loanCount: 3421
      },
      {
        id: '2',
        title: '  ',
        author: 'J.K. ',
        publisher: '',
        isbn: '9788983920089',
        category: '',
        publishYear: '1998',
        description: '    ',
        loanCount: 2987
      },
      {
        id: '3',
        title: ' ',
        author: ' ,  ',
        publisher: '',
        isbn: '9788950955671',
        category: '',
        publishYear: '2014',
        description: '        ',
        loanCount: 2847
      },
      {
        id: '4',
        title: '82 ',
        author: '',
        publisher: '',
        isbn: '9788937473371',
        category: '',
        publishYear: '2016',
        description: '     ',
        loanCount: 1923
      },
      {
        id: '5',
        title: '',
        author: ' ',
        publisher: '',
        isbn: '9788934972464',
        category: '',
        publishYear: '2015',
        description: '   ',
        loanCount: 1456
      },
      {
        id: '6',
        title: ' 1',
        author: ' ',
        publisher: '',
        isbn: '9788925208015',
        category: '',
        publishYear: '1997',
        description: '   ',
        loanCount: 1234
      }
    ];

    const filtered = dummyBooks.filter(book =>
      book.title.toLowerCase().includes(query.toLowerCase()) ||
      book.author.toLowerCase().includes(query.toLowerCase()) ||
      book.category.toLowerCase().includes(query.toLowerCase())
    );

    //    (   )
    return filtered.map(book => ({
      ...book,
      availability: generateLibraryAvailability(libraries)
    }));
  };

  //   
  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    if (searchTerm.trim()) {
      //         
      setSearchResults([]);
      setHasSearched(false);
      setSelectedRegion('');
      setLibraries([]);
      
      saveRecentSearch(searchTerm.trim());
      performSearch(searchTerm.trim());
    }
  };

  //  
  const handleReserve = (bookId: string, libraryId: number, libraryName: string, libraryPhone: string, libraryHours: string = '', isAvailable: boolean, availableBooks: number = 0, totalBooks: number = 0) => {
    //     (availableBooks === 0 && totalBooks > 0)   
    if (availableBooks === 0 && totalBooks > 0) {
      alert('    .');
      window.location.href = `tel:${libraryPhone}`;
      return;
    }

    const currentTime = new Date();
    const currentHour = currentTime.getHours();
    const isOpen = currentHour >= 9 && currentHour < 18;
    
    let message = '';
    if (isAvailable) {
      message = `${libraryName}   ?`;
    } else {
      message = `${libraryName} ?`;
    }
    
    message += `\n\n : ${libraryPhone}`;
    if (libraryHours) {
      message += `\n : ${libraryHours}`;
    }
    
    if (!isOpen) {
      message += `\n\n     .`;
      message += `\n  .`;
    }
    
    const confirmed = window.confirm(message);
    if (confirmed) {
      if (isAvailable) {
        alert('     .');
      } else {
        alert('     .');
      }
      window.location.href = `tel:${libraryPhone}`;
    }
  };

  //   
  useEffect(() => {
    if (externalSearchQuery && externalSearchQuery.trim()) {
      setSearchTerm(externalSearchQuery);
      performSearch(externalSearchQuery);
      if (onExternalSearchComplete) {
        onExternalSearchComplete();
      }
    }
  }, [externalSearchQuery, onExternalSearchComplete]);

  //     
  useEffect(() => {
    if (parentSelectedRegion !== undefined) {
      setSelectedRegion(parentSelectedRegion);
    }
  }, [parentSelectedRegion]);

  //   
  useEffect(() => {
    if (selectedRegion) {
      const regionLibraries = generateLibrariesByRegion(selectedRegion);
      setLibraries(regionLibraries);
    }
  }, [selectedRegion]);



  //      
  const updateSearchResultsWithLibraries = React.useCallback((newLibraries: Library[]) => {
    setSearchResults(prevResults => {
      if (prevResults.length > 0 && newLibraries.length > 0) {
        return prevResults.map(book => ({
          ...book,
          availability: generateLibraryAvailability(newLibraries)
        }));
      }
      return prevResults;
    });
  }, []);

  //       
  useEffect(() => {
    if (libraries.length > 0) {
      updateSearchResultsWithLibraries(libraries);
    }
  }, [libraries, updateSearchResultsWithLibraries]); // libraries   

  return (
    <Container>
      <SearchForm onSubmit={handleSearch}>
        <SearchInputContainer>
          <SearchInput
            type="text"
            placeholder="  "
            value={searchTerm}
            onChange={handleSearchInputChange}
            onFocus={handleSearchInputFocus}
            onBlur={handleAutocompleteClose}
          />
          {showAutocomplete && autocompleteItems.length > 0 && (
            <AutocompleteDropdown>
              {autocompleteItems.map((item, index) => (
                <AutocompleteItemElement key={index} onClick={() => handleAutocompleteSelect(item)}>
                  <span className="icon">
                    {item.type === 'recent' ? '' : item.type === 'popular' ? '' : ''}
                  </span>
                  <span className="text">{item.text}</span>
                  <span className="category">{item.category}</span>
                </AutocompleteItemElement>
              ))}
            </AutocompleteDropdown>
          )}
        </SearchInputContainer>
        <SearchButton type="submit" disabled={loading}>
          {loading ? ' ...' : ' '}
        </SearchButton>
      </SearchForm>

      {/*        */}
      {searchResults.length > 0 && (
        <div style={{ marginBottom: '20px' }}>
          <RegionSelector 
            onLibrariesUpdate={setLibraries} 
            selectedRegion={selectedRegion} 
            onRegionUpdate={(region) => {
              setSelectedRegion(region);
              if (onRegionUpdate) {
                onRegionUpdate(region);
              }
              //       
              const regionLibraries = generateLibrariesByRegion(region);
              setLibraries(regionLibraries);
            }} 
          />
        </div>
      )}



      {apiStatus && (
        <ApiStatus isError={!!apiError}>
          {apiStatus}
        </ApiStatus>
      )}

      {loading && (
        <LoadingDiv>
             API  ...
        </LoadingDiv>
      )}

      {!loading && hasSearched && searchResults.length === 0 && (
        <EmptyState>
          <div className="emoji"></div>
          <div className="title">  </div>
          <div className="subtitle">  </div>
        </EmptyState>
      )}

      {!loading && searchResults.length > 0 && (
        <SearchResults>
          {searchResults.map((book) => (
            <BookCard key={book.id}>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', marginBottom: '15px' }}>
                <BookTitle>{book.title}</BookTitle>
                <button
                  onClick={() => toggleBookmark(book.id)}
                  style={{
                    background: 'none',
                    border: 'none',
                    fontSize: '24px',
                    cursor: 'pointer',
                    color: isBookmarked(book.id) ? '#ff6b6b' : '#ccc',
                    transition: 'color 0.3s'
                  }}
                  title={isBookmarked(book.id) ? ' ' : ' '}
                >
                  {isBookmarked(book.id) ? '' : ''}
                </button>
              </div>
              <BookInfo>
                <span>: {book.author}</span>
                <span>: {book.publisher}</span>
                <span>: {book.category}</span>
                <span>: {book.publishYear}</span>
                {book.loanCount && <span> : {book.loanCount.toLocaleString()}</span>}
              </BookInfo>
              
              {book.description && (
                <BookDescription>
                  {book.description}
                </BookDescription>
              )}
              
              {selectedRegion && (
                <AvailabilitySection>
                  <AvailabilityTitle>
                     {selectedRegion}   
                  </AvailabilityTitle>
                  <LibraryList>
                    {book.availability
                    .sort((a, b) => {
                      if (a.available && !b.available) return -1;
                      if (!a.available && b.available) return 1;
                      if (a.available && b.available) return a.waitingList - b.waitingList;
                      return a.waitingList - b.waitingList;
                    })
                    .map((avail) => (
                    <LibraryItem key={avail.libraryId}>
                      <LibraryHeader>
                        <LibraryName>
                          {avail.available ? ' ' : ' '}
                          {avail.libraryName}
                        </LibraryName>
                        <LibraryAddress> {avail.libraryAddress}</LibraryAddress>
                        <LibraryPhone> {avail.libraryPhone}</LibraryPhone>
                        <LibraryHours> {avail.libraryHours || '09:00-18:00'}</LibraryHours>
                      </LibraryHeader>
                      <LibraryStats>
                        {avail.availableBooks > 0 && (
                          <div style={{ 
                            background: '#e8f5e8', 
                            border: '1px solid #4CAF50',
                            borderRadius: '8px',
                            padding: '12px',
                            marginBottom: '10px'
                          }}>
                            <div style={{ 
                              display: 'flex', 
                              alignItems: 'center', 
                              gap: '8px',
                              marginBottom: '8px',
                              fontWeight: 'bold',
                              color: '#2e7d32'
                            }}>
                                  
                            </div>
                            <div style={{ 
                              fontSize: '14px',
                              color: '#1b5e20',
                              lineHeight: '1.4'
                            }}>
                               {avail.location}
                            </div>
                            <div style={{ 
                              fontSize: '14px',
                              color: '#2e7d32',
                              marginTop: '4px'
                            }}>
                                 {avail.availableBooks}
                            </div>
                          </div>
                        )}
                      </LibraryStats>
                      
                      {avail.availableBooks === 0 && avail.totalBooks > 0 && (
                        <div style={{ 
                          background: '#e8f5e8', 
                          border: '1px solid #4CAF50',
                          borderRadius: '8px',
                          padding: '12px',
                          margin: '10px 0',
                          fontSize: '14px',
                          color: '#2e7d32'
                        }}>
                               .
                        </div>
                      )}
                      
                      {avail.totalBooks === 0 && (
                        <div style={{ 
                          background: '#f8d7da', 
                          border: '1px solid #f5c6cb',
                          borderRadius: '8px',
                          padding: '12px',
                          margin: '10px 0',
                          fontSize: '14px',
                          color: '#721c24'
                        }}>
                                 .
                        </div>
                      )}
                      
                      <div style={{ 
                        display: 'flex', 
                        justifyContent: 'space-between', 
                        alignItems: 'center',
                        marginBottom: '10px'
                      }}>
                        {avail.waitingList > 0 && (
                          <div style={{ 
                            fontSize: '14px',
                            fontWeight: 'bold',
                            color: 'var(--accent-color)',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '6px'
                          }}>
                               {avail.waitingList}
                          </div>
                        )}
                        <div style={{ marginLeft: 'auto' }}>
                          <ReserveButton 
                            available={avail.available || (avail.availableBooks === 0 && avail.totalBooks > 0)}
                            onClick={() => handleReserve(
                              book.id,
                              avail.libraryId,
                              avail.libraryName,
                              avail.libraryPhone,
                              avail.libraryHours || '09:00-18:00',
                              avail.available,
                              avail.availableBooks,
                              avail.totalBooks
                            )}
                            disabled={avail.totalBooks === 0}
                          >
                            {avail.totalBooks === 0 ? '  ' : '  '}
                          </ReserveButton>
                        </div>
                      </div>
                    </LibraryItem>
                  ))}
                </LibraryList>
              </AvailabilitySection>
            )}
            </BookCard>
          ))}
        </SearchResults>
      )}
    </Container>
  );
};

export default BookSearchSection;