/**
 *   API  (  )
 */
export const searchBooksAPI = async (
  query: string,
  pageNo: number = 1,
  pageSize: number = 10
): Promise<BookSearchResult[]> => {
  const apiService = new LibraryAPIService({
    baseURL: BASE_URL,
    apiKey: AUTH_KEY
  });
  
  return await apiService.searchBooks({
    query,
    pageNo,
    pageSize
  });
};

